/* contact page css */

.contact-left.column{
  background: #f2f2f2;
}

.contact-right.column {
  background: #2c3e50;
  color: white;
  padding: 1em;
}

.contact-grid h2 {
  font-family: 'Anton', sans-serif;
}

.contact-grid p {
  font-family: 'Oxygen', sans-serif;
  font-weight: bold;
}

.contact-grid hr {
  border-top: 5px dotted black;
  width: 50%;
  margin: auto;
}

.contact-list i {
  font-size: 66px;
  padding-right: 5rem;
}
.contact-me {
  font-size: 40px;
  padding-right: 5rem;
}
.contact-right-col {
  background: #2c3e50;
  color: white;
  padding: 2em;
}
.contact-left-col {
  background: #f2f2f2;
  padding: 1em;
}

/* contact page css */

.avatar-img {
  height: 250px;
  padding-top: 5em;
}

.banner-text {
  background-color: #2c3e50;
  opacity: .8;
  width: 75%;
  height: 80%;
  margin: auto;
  border-radius: 0px;
}

.banner-text h1 {
  font-size: 48px;
  font-weight: bold;
  color: white;
}

.banner-text hr {
  border-top: 3px solid white;
  width: 80%;
  margin: auto;
}

.banner-text p {
  color: white;
  font-size: 20px;
  padding: 1em;
}

.social-links {
  -webkit-justify-content: space-between;
          justify-content: space-between;
  width:60%;
  margin: auto;
}

.social-links i {
 color: #2c3e50;
  font-size: 5em;
  padding: 8px;
}

/* Project page */

.bm-burger-button {
    position: fixed;
    width: 36px;
    height: 20px;
    left: 25px;
    top: 25px;
}

/* Color/shape of burger icon bars */
.bm-burger-bars {
    background: #2c3e50;
}

/* Color/shape of burger icon bars on hover*/
.bm-burger-bars-hover {
    background: #667db6;
}

/* Position and sizing of clickable cross button */
.bm-cross-button {
    height: 24px;
    width: 24px;
    background-color: black;
}

/* Color/shape of close button cross */
.bm-cross {
    background: #bdc3c7;
}

/*
Sidebar wrapper styles
Note: Beware of modifying this element as it can break the animations - you should not need to touch it in most cases
*/
.bm-menu-wrap {
    position: fixed;
    height: 100%;
}

/* General sidebar styles */
.bm-menu {
    background: #2c3e50;
    padding: 2.5em 1.5em 0;
    font-size: 1.15em;
}

/* Morph shape necessary with bubble or elastic */
.bm-morph-shape {
    fill: #373a47;
}

/* Wrapper for item list */
.bm-item-list a {
    color: #b8b7ad;
    padding: 0.8em;
}

/* Individual item */
.bm-item {
    display: inline-block;
}

/* Styling of overlay */
.bm-overlay {
    background: rgba(0, 0, 0, 0.3);
}


.content-upp:hover{
    opacity: 0.2;
    filter: alpha(opacity=80);
}

* {box-sizing: border-box;}

.contactmeform {
    max-width: 420px;
    margin: 0 auto;
}
p, h3 {
    text-align: center;
    font-weight: 500;
}

input[type=text],input[type=email], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 5px;
    resize: vertical;
}

input[type=submit] {
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-left: 38%;

}

input[type=submit]:hover {
    background-color: #45a049;
}

.container {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}
.sucsess  {
    margin: 20px 0;
    border: 1px solid green;
    padding: 7px;
    font-weight: bold;
}

.error {
    margin: 20px 0;
    border: 1px solid red;
    padding: 7px;
    font-weight: bold;
}
