* {box-sizing: border-box;}

.contactmeform {
    max-width: 420px;
    margin: 0 auto;
}
p, h3 {
    text-align: center;
    font-weight: 500;
}

input[type=text],input[type=email], select, textarea {
    width: 100%;
    padding: 12px;
    border: 1px solid #ccc;
    border-radius: 4px;
    box-sizing: border-box;
    margin-top: 6px;
    margin-bottom: 5px;
    resize: vertical;
}

input[type=submit] {
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    margin-left: 38%;

}

input[type=submit]:hover {
    background-color: #45a049;
}

.container {
    border-radius: 5px;
    background-color: #f2f2f2;
    padding: 20px;
}
.sucsess  {
    margin: 20px 0;
    border: 1px solid green;
    padding: 7px;
    font-weight: bold;
}

.error {
    margin: 20px 0;
    border: 1px solid red;
    padding: 7px;
    font-weight: bold;
}